@import './shared/_variables.sass';

.settingsContainer {
    width: 100%;
    position: relative;
    padding: $FONT-SIZE;
    padding-top: $FONT-SIZE * 3;
    margin-top: $FONT-SIZE * 2; }

.gearIcon {
    position: absolute;
    right: $FONT-SIZE * 1.5;
    top: $FONT-SIZE;
    font-size: $FONT-SIZE * 1.5;
    filter: drop-shadow( 3px 4px 1px #CCCCCC );

    &:hover {
        cursor: pointer;
        color: dimgray; } }

.settingsPane {
    display: flex;
    flex-direction: column;
    float: right;
    margin-top: 0.5 * $FONT-SIZE; }

.setting {
    margin-bottom: 0.5 * $FONT-SIZE; }

.numRowLabel {
    font-size: 0.6 * $FONT-SIZE;
    display: flex;
    flex-direction: column;
    justify-content: space-around; }

.inputLabels {
    display: flex;
    justify-content: space-between; }

.timeNum {
    text-align: center;
    max-width: $FONT_SIZE; }

.infIcon {
    font-size: 0.9 * $FONT-SIZE; }

.timeUnit {
    font-size: 0.5 * $FONT-SIZE;
    margin-left: -100%;
    margin-right: -100%; }

.nonActive {
    color: darkgray; }

.slider {
    width: 10 * $FONT-SIZE; }
