@import './shared/_variables.sass';

.wordsAreaContainer {
  width: 100%;
  // height: 4 * $FONT-SIZE
  overflow: hidden;
  margin-bottom: 0.7 * $FONT-SIZE;
  margin-top: $FONT-SIZE; }

.wordsArea {
  font-size: 1 * $FONT-SIZE;
  height: 2 * $FONT-SIZE;
  padding: 0.5 * $FONT-SIZE;
  padding-top: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.wordWrapper {
  height: 2 * $FONT-SIZE;
  position: relative;
  display: flex;
  flex-direction: row; }

.letter {
  height: 100%;
  line-height: 2 * $FONT-SIZE; }

.letterFocus {
  background-color: lightgray;
  border-bottom: 2px solid black; }

.letterCorrect {
  background-color: lightgreen;
  position: relative;
  &::before {
    content: '';
    opacity: 0.5;
    position: absolute;
    transform-origin: bottom;
    background-color: gray;
    width: 100%;
    height: 100%;
    margin-top: 0;
    animation: growUp 0.3s forwards; } }

@keyframes growUp {
  0% {
    transform: scale(0);
    opacity: 1; }
  100% {
    transform: scale(3);
    opacity: 0; } }

.letterIncorrect {
  background-color: firebrick; }

.space::after {
    content: ',';
    visibility: hidden; }
