@import './shared/_variables.sass';

$BIGGER-STAT: 10 * $FONT-SIZE;
$SMALLER-STAT: 5 * $FONT-SIZE;
$TEXT-SHADOW: 2px 3px 1px #CCCCCC;

.endGameCtn {
    width: 100%;
    text-align: center;

    * {
        text-shadow: $TEXT-SHADOW; } }

.title {
    font-size: 2.1 * $FONT-SIZE;
    margin-left: 4 * $FONT-SIZE; }

.statCtn {
    display: flex;
    justify-content: space-evenly;
    height: $BIGGER-STAT;
    width: 100%; }

.alignBtmCtn {
    height: 7.6 * $FONT-SIZE;
    position: relative;

    &::before {
        display: inline-block;
        height: 100%;
        content: ''; } }

.alignBtmText {
    vertical-align: baseline; }

.speedText {
    font-size: $BIGGER-STAT; }

.speedUnit {
    font-size: 1.3 * $FONT-SIZE; }

.noMissed {
    margin-top: 3 * $FONT-SIZE; }

.accLabel {
    font-size: 1.3 * $FONT-SIZE;
    text-align: right; }

.adjLabel {
    font-size: 0.6 * $FONT-SIZE;
    text-align: right; }

.accNum {
    font-size: $SMALLER-STAT; }

.percent {
    font-size: $SMALLER-STAT * 0.7;
    text-shadow: $TEXT-SHADOW; }

.wrongKeysCtn {
    display: flex;
    justify-content: space-between;
    margin-left: 7 * $FONT-SIZE;
    margin-right: 5 * $FONT-SIZE;
    margin-top: 3.5 * $FONT-SIZE; }

.tableLabel {
    padding-top: 3 * $FONT-SIZE;
    align-self: top;
    font-size: 0.9 * $FONT-SIZE;
    white-space: pre-wrap;
    width: 6 * $FONT-SIZE;
    margin-right: $FONT-SIZE; }

.table {
    // font-size: 2 * $FONT-SIZE
 }    // border: 1px solid black

table, th, td {
    border-collapse: collapse;
    border: 1px solid black;
    padding: 0.5 * $FONT-SIZE 1.5 * $FONT-SIZE;
    font-size: 1.5 * $FONT-SIZE; }

table {
    box-shadow: $TEXT-SHADOW; }

.accCtn {
    display: flex;
    flex-direction: column;
    align-self: flex-end; }

.tooltip {
    align-self: flex-end;
    position: relative;
    display: inline-block;
    align-text: right;
    border-bottom: 1px dotted black; }

.tooltiptext {
    visibility: hidden;
    margin-top: 0.3 * $FONT-SIZE;
    margin-left: 0.7 * $FONT-SIZE;
    width: 8 * $FONT-SIZE;
    background-color: black;
    text-align: left;
    box-shadow: 4px 4px 1px dimgray;
    color: #fff;
    padding: 0.5 * $FONT-SIZE;
    position: absolute;
    text-shadow: none;
    white-space: break-spaces;
    z-index: 1; }

.tooltip:hover {
    cursor: help; }

.tooltip:hover .tooltiptext {
  visibility: visible; }

