@import './shared/_variables.sass';

.statsCtn {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-left: $FONT-SIZE;
  padding-right: $FONT-SIZE;
  padding-left: 45%; }

.speedInd {
  display: flex; }

.speedText {
  font-size: 1.7 * $FONT-SIZE; }

.speedUnit {
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 0.3 * $FONT-SIZE;
  margin-left: 0.3 * $FONT-SIZE;
  >:first-child {
    font-size: 0.55 * $FONT-SIZE; }

  >:nth-child(2) {
    text-align: right;
    font-size: 0.7 * $FONT-SIZE; } }

.timerWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around; }

.timer {
  font-size: 1.5 * $FONT-SIZE; }
